import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { Trans } from "@lingui/react/macro";
import { motion, AnimatePresence } from "framer-motion";
import { supabase } from "../../lib/supabaseClient";

function Propuestas() {
  const [propuestasData, setPropuestasData] = useState([]);
  const [currentIndex, setCurrentIndex] = useState(0);
  const navigate = useNavigate();

  useEffect(() => {
    async function fetchPropuestas() {
      const { data, error } = await supabase
        .from("propuestas")
        .select("*")
        .order("id", { ascending: true });
      if (error) console.error(error);
      else setPropuestasData(data);
    }
    fetchPropuestas();
  }, []);

  const prevSlide = () => {
    setCurrentIndex((prev) =>
      prev === 0 ? propuestasData.length - 1 : prev - 1
    );
  };

  const nextSlide = () => {
    setCurrentIndex((prev) =>
      prev === propuestasData.length - 1 ? 0 : prev + 1
    );
  };

  if (propuestasData.length === 0) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-cyan-600 text-white">
        <p>Cargando propuestas...</p>
      </div>
    );
  }

  const current = propuestasData[currentIndex];

  return (
    <div className="relative min-h-screen flex flex-col justify-center items-center bg-cyan-600 text-white overflow-hidden">
      {/* Fondo animado */}
      <AnimatePresence mode="wait">
        <motion.div
          key={currentIndex}
          initial={{ opacity: 0, scale: 1.2 }}
          animate={{ opacity: 1, scale: 1 }}
          exit={{ opacity: 0.1, scale: 0.8 }}
          transition={{ duration: 0.4 }}
          className="absolute inset-0"
          style={{
            backgroundImage: `url(${current.image})`,
            backgroundSize: "auto 140%",
            backgroundPosition: "center",
            backgroundRepeat: "no-repeat",
            WebkitMaskImage:
              "radial-gradient(circle at center, rgba(0,0,0,1) 15%, rgba(0,0,0,0.5) 40%, rgba(0,0,0,0) 85%)",
            WebkitMaskRepeat: "no-repeat",
            WebkitMaskPosition: "center",
            maskImage:
              "radial-gradient(circle at center, rgba(0,0,0,1) 0%, rgba(0,0,0,0.2) 50%, rgba(0,0,0,0) 90%)",
            maskRepeat: "no-repeat",
            maskPosition: "center",
          }}
        ></motion.div>
      </AnimatePresence>

      {/* Contenido */}
      <div className="relative z-10 text-center flex-grow flex flex-col items-center justify-center">
        <AnimatePresence mode="wait">
          <motion.div
            key={currentIndex + "-text"}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            transition={{ duration: 0.4 }}
            className="w-[75%] mx-auto flex flex-col"
          >
            <button
              onClick={() => navigate("/programa?selectedId=" + current.id)}
              className="md:text-4xl lg:text-5xl xl:text-5xl 2xl:text-7xl 3xl:text-8xl font-bold mb-24 hover:text-yellow-300 cursor-pointer transition-colors duration-300"
            >
              {current.titulo}
            </button>

            <p className="md:text-md xl:text-xl 2xl:text-2xl 3xl:text-3xl font-helvetica text-white text-center">
              {current.resumen}
            </p>
          </motion.div>
        </AnimatePresence>
      </div>

      {/* Botonera inferior */}
      <div className="relative z-20 mt-12 w-[50%] mx-auto flex flex-col items-center mb-12">
        {/* Texto fijo */}
        <p className="md:text-md 3xl:text-lg font-bold font-helvetica text-yellow-400 mb-2">
          <Trans>LOOK UP WHAT WE CAN ACHIEVE TOGETHER</Trans>
        </p>

        {/* Botones */}
        <div className="flex gap-4">
          <button
            onClick={() => navigate("/programa?filtro=Estudiante")}
            className="md:text-sm 3xl:text-xl px-6 py-4 bg-white text-blue-700 text-[18px] font-bold font-helvetica rounded-xl shadow-md hover:bg-gray-200 transition"
          >
            <Trans>STUDENT</Trans>
          </button>
          <button
            onClick={() => navigate("/programa?filtro=PDI")}
            className="md:text-sm 3xl:text-xl px-6 py-4 bg-white text-blue-700 font-bold font-helvetica rounded-xl shadow-md hover:bg-gray-200 transition"
          >
            <Trans>PDI</Trans>
          </button>
          <button
            onClick={() => navigate("/programa?filtro=PTGAS")}
            className="md:text-sm 3xl:text-xl px-6 py-4 bg-white text-blue-700 font-bold font-helvetica rounded-xl shadow-md hover:bg-gray-200 transition"
          >
            <Trans>PTGAS</Trans>
          </button>
        </div>
        <p className="md:text-sm 3xl:text-xl font-bold font-helvetica text-yellow-400 mb-2 mt-2">
          OR EXPLORE
        </p>
        <Link
          to="/programa"
          className="md:text-sm 3xl:text-xl px-6 py-4 bg-yellow-400 text-blue-700 font-bold font-helvetica rounded-xl shadow-md hover:bg-yellow-300 transition "
        >
          <Trans>OUR PROGRAM</Trans>
        </Link>
      </div>

      {/* Botones de slides */}
      <button
        onClick={prevSlide}
        className="absolute left-4 md:left-12 top-1/2 -translate-y-1/2 z-20 bg-yellow-400 p-3 rounded-full hover:bg-black/60 transition"
      >
        &#10094;
      </button>
      <button
        onClick={nextSlide}
        className="absolute right-4 md:right-12 top-1/2 -translate-y-1/2 z-20 bg-yellow-400 p-3 rounded-full hover:bg-yellow-300 transition"
      >
        &#10095;
      </button>
    </div>
  );
}

export default Propuestas;
