import React, { useRef, useState, useEffect } from "react";
import { CSSTransition } from "react-transition-group";
import Navbar from "../../components/Navbar";
import Footer from "../../components/Footer";
import AgendaYNoticias from "./AgendaYNoticias";
import Divider from "../../components/Divider";

function NewsAndSchedulePage() {
  const nodeRef = useRef(null);
  const [show, setShow] = useState(false);

  useEffect(() => {
    setShow(true);
  }, []);

  return (
    <div className="bg-black">
      <CSSTransition
        in={show}
        timeout={300}
        classNames="fade"
        unmountOnExit
        nodeRef={nodeRef}
      >
        <div ref={nodeRef} className="font-serif text-gray-900">
          <Navbar />
          <main>
            <Divider height="96px" />
            <section id="noticias" className="scroll-mt-48">
              <AgendaYNoticias />
            </section>
          </main>
          <Divider height="96px" />
          <Footer />
        </div>
      </CSSTransition>
    </div>
  );
}

export default NewsAndSchedulePage;
